import { MyNavbar } from "../modules/Navbar/Navbar"
import { Layout } from 'antd';
import { MyFooter } from "../modules/Footer/components/Footer";
import { LoginBody } from "../modules/LoginBody/Loginbody";

const { Header, Footer, Content } = Layout;
export const LoginPage = ()=>{
    return(<>
    <Layout>
      <Header>
    <MyNavbar/>
    </Header>
    <Content>
    <LoginBody/>
    </Content>
    <Footer>
        <MyFooter/>
    </Footer>
    </Layout>
    </>)
}
