import { MyNavbar } from "../modules/Navbar/Navbar"
import { Layout } from 'antd';
import { MyFooter } from "../modules/Footer/components/Footer";
import { Compose } from "../modules/ComposeBody/compose";

const { Header, Footer,  Content } = Layout;

export const ComposeBlog = ()=>{
    return(<>
    <Layout>
      <Header>
    <MyNavbar/>
    </Header>
    <Content>
        <Compose/>
    </Content>
    <Footer>
        <MyFooter/>
    </Footer>
    </Layout>
    </>)
}