import { MyNavbar } from "../modules/Navbar/Navbar"
import { Layout } from 'antd';
import { MyFooter } from "../modules/Footer/components/Footer";
import { RegisterBody } from "../modules/RegisterBody/RegisterBody";

const { Header, Footer, Content } = Layout;
export const RegisterPage = ()=>{
    return(<>
    <Layout>
      <Header>
    <MyNavbar/>
    </Header>
    <Content>
    <RegisterBody/>
    </Content>
    <Footer>
        <MyFooter/>
    </Footer>
    </Layout>
    </>)
}
