import Box from "@mui/material/Box";
import TextField from "@mui/material/TextField";
import { useRef, useState } from "react";
import Button from "@mui/material/Button";
import { API_CLIENT } from "../../Shared/services/API_CLIENT";

export const RegisterBody = () => {
  const [message, setMessage] = useState("");
  const name = useRef("");
  const email = useRef("");
  const pass = useRef("");

  const register = async () => {
    const result = await API_CLIENT.post(process.env.REACT_APP_REGISTER, {
      name: name.current.value,
      email: email.current.value,
      pass: pass.current.value,
    });
    console.log(result);

    setMessage(result.data);
    name.current.value = "";
    email.current.value = "";
    pass.current.value = "";
  };

  return (
    <>
      <Box
        component="form"
        sx={{
          "& > :not(style)": { m: 1, width: "25ch" },
          display: "block",
          margin: "auto",
          width: "50%",
          minHeight: "fit-content",
          backgroundColor: "#FFFFFF",
          marginTop: "10vh",
          boxShadow: "0 0 10px 5px #DEE0E3",
          borderRadius: "10px",
        }}
        noValidate
        autoComplete="off"
      >
        <div>
          <div>
            <h2>Sign Up</h2>
            <br />
          </div>
          <TextField
            inputRef={name}
            id="filled-basic"
            label="Full Name"
            variant="filled"
          />
          <br />
          <br />
          <TextField
            inputRef={email}
            id="filled-basic"
            label="Email"
            variant="filled"
          />
          <br />
          <br />
          <TextField
            inputRef={pass}
            type="password"
            id="filled-basic"
            label="Password"
            variant="filled"
          />

          <br />
          <br />
          <Button variant="contained" onClick={register}>
            Submit
          </Button>
          <h2>{message}</h2>
        </div>
      </Box>
    </>
  );
};