import { Layout } from "antd";
import React from "react";
import { SlideImages } from "../modules/HomeBody/CarouselText";
import { MyFooter } from "../modules/Footer/components/Footer";
import { Homebody } from "../modules/HomeBody/homebody";
import { MyNavbar } from "../modules/Navbar/Navbar";

const { Header, Footer,  Content } = Layout;

const Homepage = () => {
  return (
    <>
      <Layout>
        <Header>
          <MyNavbar />
        </Header>
        <div>
          <Content>
            <div className="carousel">
              <SlideImages />
            </div>
            <div>
              <Homebody />
            </div>
          </Content>
        </div>
        <div>
          <Footer>
            <MyFooter />
          </Footer>
        </div>
      </Layout>
    </>
  );
};
export default Homepage;
